.wrapper {
    display: flex;
    align-items: center;
}

.header {

    background-size: cover;
    margin: auto;
    @media screen and (min-width: 320px) {
            background-image: url(../images/abstract320.jpg);
        }
    
        @media screen and (min-width: 768px) {
            background-image: url(../images/abstract768.jpg);
        }
    
        @media screen and (min-width: 1200px) {
            background-image: url(../images/abstract.jpg);
        }
}


.logo {
    display: flex;
    align-items: center;
}

.logo_secondpart {
    margin-left: 10px;
    @media screen and (min-width: 320px) {
            visibility: hidden;
        }
    @media screen and (min-width: 768px) {
            visibility: visible;
        }
}

.wrapper {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    padding-bottom: 141px;
}

.nav_menu {
    display: flex;
    font-size: 12px;
    line-height: 14px;
    /* identical to box height */
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
}

.nav_link {
    position: relative;

    &::before {
            content: " ";
            position: absolute;
            left: 0;
            bottom: -5px;
            width: 100%;
            height: 2px;
            background-color: rgba(255, 0, 27, 1);
            transition: transform 0.35s;
            transform: scaleX(0);
            transform-origin: left;
        }

        &:hover:before {
            width: 100%;
            transform: scaleX(1);
        
        }
        &:not(:last-child){
            margin-right: 40px;
        }

}
// клас для активної кнопки
.active {
    margin-right: 40px;
    &::after {
        position: absolute;
        content: '';
        width: 100%;
        height: 4px;
        background-color: rgba(255, 0, 27, 1);
        border-radius: 2px;
        bottom: -1px;
        left: 0;
    }
}
// клас для сторінки my Library, змінює фон
.mylibrary{
    @media screen and (min-width: 320px) {
            background-image: url(../images/Rectangle328.jpg);
        }
    
        @media screen and (min-width: 768px) {
            background-image: url(../images/Rectangle728.jpg);
        }
    
        @media screen and (min-width: 1200px) {
            background-image: url(../images/Rectangle1200.jpg);
        }
}
